class TestPage extends Component {

  constructor(props) {
    super(props);
    this.pulledToBottom = this.pulledToBottom.bind(this);

    this.state = {
      bottom: false,
      controls: {
        text: '',
        number: '',
        tel: '',
        email: '',
        date: '',
        time: '',
        select: '',
        textarea: ''
      },
      form2: {
        action: '/form',
        controls: [
          {
            name: 'mobile',
            type: 'tel',
            placeholder: '',
            required: true
          },
          {
            name: 'code',
            type: 'number',
            placeholder: '',
            required: true,
            maxLength: 4,
            minLength: 4
          }
        ],
        submitText: '',
        beforeSubmit(formData) {
          // console.log(formData);
        },
        onSubmit(formData, form) {
          console.log(formData, form);
        }
      }
    };
  }

  render() {
    const { bottom, controls, form2 } = this.state;

    return (
      <div>
        <h2 className="gap-side gap-t"></h2>
        <div className="gap-side">
          <Icon name="loading"></Icon>
          <Icon name="loading" rotate className="gap-l"></Icon>
        </div>

        <h2 className="gap-side gap-5"></h2>
        <Loading />

        <h2 className="gap-side gap-t"></h2>
        <div className="gap-side">
          <Button disabled onTouchTap={this.handleEvents}></Button>
          <Button type="button" onTouchTap={this.handleEvents} className="gap-t"></Button>
          <Button type="button" icon="rotate" className="gap-t" disabled></Button>
        </div>

        <h2 className="gap-side gap-t"></h2>
        <div className="gap-side">
          <Button disabled icon="delete" link className="gap-r"></Button>
          <Button icon="edit" link></Button>
        </div>

        <h2 className="gap-side gap-t"></h2>
        <div className="gap-side">
          <Calendar {...calendarData} />
        </div>

        <h2 className="gap-side gap-t"></h2>
        <div className="gap-side">
          <CheckButton className="gap-r" onToggle={this.handleEvents}></CheckButton>
          <CheckButton checked onToggle={this.handleEvents}></CheckButton>
        </div>

        <h2 className="gap-side gap-t"></h2>
        <div className="gap-side">
          <ChoiceList data={choiceData} onToggle={this.handleEvents} />
        </div>

        <h2 className="gap-side gap-t"> ()</h2>
        <Dropdown {...dropDownData} onFilter={this.filter} />

        <h2 className="gap-side gap-t"></h2>
        <div className="gap-side">
          <FormControl name="text" type="text" placeholder="" value={controls.text} onChange={this.controlChange.bind(this)} required />
          <FormControl name="number" type="number" placeholder="5-10" max="10" min="5" value={controls.number} onChange={this.controlChange.bind(this)}  />
          <FormControl name="email" type="email" placeholder="" value={controls.email} onChange={this.controlChange.bind(this)}  />
          <FormControl name="tel" type="tel" placeholder="" value={controls.tel} onChange={this.controlChange.bind(this)}  />
          <FormControl name="date" type="date" placeholder="" required value={controls.date} onChange={this.controlChange.bind(this)}  />
          <FormControl name="time" type="time" placeholder="" required value={controls.time} onChange={this.controlChange.bind(this)}  />
          <FormControl name="select" type="select" placeholder="" options={formSelectOptions} required value={controls.select} onChange={this.controlChange.bind(this)}  />
          <FormControl name="textarea" type="textarea" placeholder="510 " maxLength="10" minLength="5" value={controls.textarea} onChange={this.controlChange.bind(this)}  />
        </div>

        <h2 className="gap-side gap-t"></h2>
        <Form {...formData}></Form>

        <h2 className="gap-side gap-t"></h2>
        <Form {...form2} className="gap-t" onControlChange={this.formControlChange.bind(this)} />

        <h2 className="gap-side gap-t">Tab.Nav</h2>
        <Tab.Nav data={tabNavData}></Tab.Nav>

        <h2 className="gap-side gap-t">Tab.Scope</h2>
        <Tab.Scope data={tabScopeData} style={{ position: 'relative' }}></Tab.Scope>

        <h2 className="gap-side gap-t">Tab.Bar ()</h2>
        <Tab.Bar data={tabBarData}></Tab.Bar>

        <h2 className="gap-side gap-t">Table</h2>
        <Table data={tableData} expanded={true}>
          <span></span>
          <span className="gap-l"></span>
          <span className="text-primary">600</span>
          <span className="gap-l"></span>
          <span className="text-primary">600</span>
        </Table>

        <h2 className="gap-side gap-t">ImageBox</h2>
        <div className="gap-side">
          <div style={{ display: 'inline-block' }}>
            <ImageBox src="example/images/200_150.jpeg"></ImageBox>
          </div>
          <div style={{ display: 'inline-block' }} className="gap-l">
            <ImageBox src="example/images/150_200.jpeg"></ImageBox>
          </div>
        </div>

        <h2 className="gap-side gap-t">ImageSlider</h2>
        <ImageSlider data={imageSliderData}></ImageSlider>

        <h2 className="gap-side gap-t"></h2>
        <div className="gap-side">
          <Button onTouchTap={this.message}></Button>
        </div>

        <h2 className="gap-side gap-t"></h2>
        <div className="gap-side">
          <Button onTouchTap={this.mask}></Button>
        </div>

        <h2 className="gap-side gap-t"></h2>
        <div className="gap-side">
          <PlusMinus value={10} min={0} max={100} onChange={this.handleEvents}/>
        </div>

        <h2 className="gap-side gap-t">TopAction ()</h2>
        <TopAction></TopAction>

        <h2 className="gap-side gap-t">PullLoader ({ bottom ? '' : ' onPull'})</h2>
        <PullLoader onPull={this.pulledToBottom} loading></PullLoader>
      </div>
    );
  }

  controlChange(name, value) {
    this.state.controls[name] = value;
    this.setState(this.state);
  }

  formControlChange(name, value) {
    Form.updateValue(this.state.form2.controls, name, value);

    this.setState({
      form2: this.state.form2
    });
  }

  filter(query) {
    console.log(query);
  }

  handleEvents(e) {
    console.log(arguments);
  }

  message() {
    Message(Date.now());
  }

  mask() {
    Mask.open({
      onTouchTap: Mask.close
    });
  }

  pulledToBottom() {
    this.setState({
      bottom: true
    });
  }
}

ReactDOM.render((
  <TestPage></TestPage>
), document.getElementById('app'));



